<template>
  <div class="flex-user">
    <div
      style="
        background-color: #fff;
        margin-bottom: 10px;
        padding: 12px 24px;
        box-sizing: border-box;
        border-radius: 4px;
      "
    >
      <a-form :form="form" layout="vertical">
        <a-row :gutter="24">
          <a-col :span="4">
            <a-form-item label="批次编号">
              <a-input
                placeholder="请输入批次编号"
                v-model="form.containerCode"
              ></a-input>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item label="批次名称">
              <a-input
                placeholder="请输入批次名称"
                v-model="form.containerName"
              ></a-input>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item label="批次类型">
              <a-select
                placeholder="请选择批次类型"
                v-model="form.containerType"
              >
                <a-select-option value="生产批次">生产批次</a-select-option>
                <a-select-option value="质检批次">质检批次</a-select-option>
                <a-select-option value="库存批次">库存批次</a-select-option>
                <a-select-option value="运输批次">运输批次</a-select-option>
                <a-select-option value="销售批次">销售批次</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item label="状态">
              <a-select
                placeholder="请选择状态"
                v-model="form.status"
              >
                <a-select-option value="进行中">进行中</a-select-option>
                <a-select-option value="已完成">已完成</a-select-option>
                <a-select-option value="已取消">已取消</a-select-option>
                <a-select-option value="已暂停">已暂停</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item label="优先级">
              <a-select
                placeholder="请选择优先级"
                v-model="form.priority"
              >
                <a-select-option value="高">高</a-select-option>
                <a-select-option value="中">中</a-select-option>
                <a-select-option value="低">低</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item :label="'\u00a0'">
              <g-btn
                text="查询"
                class="mr-2"
                type="primary"
                @callback="refresh"
              ></g-btn>
              <g-btn text="重置" @callback="resetForm"></g-btn>
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
    </div>

    <div
      style="
        background-color: #fff;
        padding: 12px 24px 0;
        box-sizing: border-box;
        overflow-y: hidden;
        border-radius: 4px;
      "
    >
      <g-table-bar :spacing.sync="tableSize" :columns.sync="columns" :virtualColumns="virtualColumns" @refreshTable="refresh">
        <g-btn
          text="添加"
          style="margin-right: 10px"
          @callback="addOrEditContainer"
        ></g-btn>
        <g-btn
          text="批量删除"
          type="danger"
          style="margin-right: 10px"
        ></g-btn>
        <g-btn text="导出excel" style="margin-right: 10px"></g-btn>
      </g-table-bar>
      <a-table
        id="tables"
        :columns="columns"
        :data-source="dataList"
        :scroll="{ y: tableheight }"
        :row-key="(record) => record.id"
        :pagination="pagination"
        :rowSelection="rowSelection"
        @change="tableChange"
        :loading="isTableLoading"
        :size="tableSize"
      >
        <!-- 状态 -->
        <span slot="status" slot-scope="text">
          <a-tag :color="getStatusColor(text)">{{ text }}</a-tag>
        </span>
        <!-- 优先级 -->
        <span slot="priority" slot-scope="text">
          <a-tag :color="getPriorityColor(text)">{{ text }}</a-tag>
        </span>
        <!-- 操作 -->
        <span slot="action" slot-scope="text, record">
          <a
            href="javascript:;"
            class="mr-1"
            @click="addOrEditContainer(record)"
            >编辑</a
          >
          <a href="javascript:;" class="del" @click="delContainer(record)"
            >删除</a
          >
        </span>
      </a-table>
    </div>
  </div>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";
import createModal from "@/utils/modal";
import { tableBase } from "@/utils";

export default {
  name: "container-list",
  mixins: [tableBase],
  data() {
    return {
      tableheight: 0,
      form: {
        containerCode: "",
        containerName: "",
        containerType: undefined,
        status: undefined,
        priority: undefined
      },
    };
  },

  mounted() {
    this.columns = [
      // 序号 index
      {
        title: "序号",
        dataIndex: "id",
        key: "id",
        customRender: (text, record, index) => `${index + 1}`,
        width: 80,
      },
      {
        title: "批次编号",
        dataIndex: "containerCode",
        key: "containerCode",
        ellipsis: true,
      },
      {
        title: "批次名称",
        dataIndex: "name",
        key: "name",
        ellipsis: true,
      },
      {
        title: "批次类型",
        dataIndex: "containerType",
        key: "containerType",
      },
      {
        title: "状态",
        dataIndex: "status",
        key: "status",
        scopedSlots: { customRender: "status" },
      },
      {
        title: "优先级",
        dataIndex: "priority",
        key: "priority",
        scopedSlots: { customRender: "priority" },
      },
      {
        title: "开始时间",
        dataIndex: "startTime",
        key: "startTime",
        ellipsis: true,
      },
      {
        title: "结束时间",
        dataIndex: "endTime",
        key: "endTime",
        ellipsis: true,
      },
      {
        title: "创建时间",
        dataIndex: "createdTime",
        key: "createdTime",
        sorter: true,
        ellipsis: true,
        width: 180,
      },
      {
        title: "操作",
        dataIndex: "action",
        key: "action",
        scopedSlots: { customRender: "action" },
        width: 150,
      },
    ];
    
    //this.columns数组深拷贝this.virtualColumns
    this.virtualColumns = JSON.parse(JSON.stringify(this.columns)).map(
      (item) => {
        item.checked = true;
        return item;
      }
    );
    
    this.$nextTick(() => {
      this.tableheight = getTableScroll({ id: "tables" });
    });
  },

  methods: {
    getStatusColor(status) {
      const colorMap = {
        '进行中': 'blue',
        '已完成': 'green',
        '已取消': 'red',
        '已暂停': 'orange'
      };
      return colorMap[status] || 'blue';
    },
    
    getPriorityColor(priority) {
      const colorMap = {
        '高': 'red',
        '中': 'orange',
        '低': 'green'
      };
      return colorMap[priority] || 'blue';
    },
    
    resetForm() {
      this.form = {
        containerCode: "",
        containerName: "",
        containerType: undefined,
        status: undefined,
        priority: undefined
      };
      this.refresh();
    },
    
    addOrEditContainer(record) {
      createModal(
        {
          title: record ? "编辑批次" : "添加批次",
          width: "800px",
        },
        { component: CreateOrEditContainer, props: { record: record } }
      );
    },

    fetchData(request) {
      this.isTableLoading = true;
      setTimeout(() => {
        let dataList = [];
        const containerTypes = [
          "生产批次",
          "质检批次",
          "库存批次",
          "运输批次",
          "销售批次"
        ];
        
        const statuses = ["进行中", "已完成", "已取消", "已暂停"];
        const priorities = ["高", "中", "低"];
        
        for (let i = 0; i < 200; i++) {
          const typeIndex = i % 5;
          const statusIndex = i % 4;
          const priorityIndex = i % 3;
          
          // 生成随机的时间
          const startDate = new Date(2024, 2, 1 + i % 28);
          const endDate = new Date(2024, 3 + i % 6, 1 + i % 28);
          const startTime = `${startDate.getFullYear()}-${(startDate.getMonth() + 1).toString().padStart(2, '0')}-${startDate.getDate().toString().padStart(2, '0')}`;
          const endTime = `${endDate.getFullYear()}-${(endDate.getMonth() + 1).toString().padStart(2, '0')}-${endDate.getDate().toString().padStart(2, '0')}`;

          dataList[i] = {
            id: i + 1,
            containerCode: `CT${String(i + 1).padStart(4, "0")}`,
            name: `${containerTypes[typeIndex]}${i % 9 + 1}`,
            containerType: containerTypes[typeIndex],
            status: statuses[statusIndex],
            priority: priorities[priorityIndex],
            startTime: startTime,
            endTime: endTime,
            description: `${containerTypes[typeIndex]}用于${statuses[statusIndex]}状态的管理`,
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
            modifiedBy: "admin",
            modifiedTime: "2024-03-" + (20 + i % 8) + " " + (13 + i % 10) + ":" + (i % 60),
          };
        }
        
        // 过滤
        if (this.form.containerCode) {
          dataList = dataList.filter((item) => item.containerCode.includes(this.form.containerCode));
        }
        if (this.form.containerName) {
          dataList = dataList.filter((item) => item.name.includes(this.form.containerName));
        }
        if (this.form.containerType) {
          dataList = dataList.filter((item) => item.containerType === this.form.containerType);
        }
        if (this.form.status) {
          dataList = dataList.filter((item) => item.status === this.form.status);
        }
        if (this.form.priority) {
          dataList = dataList.filter((item) => item.priority === this.form.priority);
        }
        
        request.finishedCallback && request.finishedCallback();
        request.successCallback &&
          request.successCallback({ items: dataList, totalCount: 200 });
          
        this.isTableLoading = false;
      }, 500);
    },

    delContainer(record) {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: `确认删除批次 "${record.name}" 吗?`,
        onOk: () => {
          this.isTableLoading = true;
          setTimeout(() => {
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.flex-user {
  box-sizing: border-box;
}
</style>